import {ref} from "vue"
import {message} from "ant-design-vue"

export const usecounter = (current = 0,{min,max}:{min?:number,max?:number},delay = 1000) =>{
    if(current < min! ){
        current = min!
        message.error("默认数值不能小于最小值")
    }else if (current > max! ){
        current = max!
        message.error("默认数值不能大于最大值")
    }
    const count = ref<number>(current)
    //增加
    const add = () =>{
        if(count.value < max!)count.value++
    }
    //减少
    const reduce = () =>{
        if(count.value > min!)count.value--
    }
    //设置
    const setup = (num: number) => {
        count.value = num
    }
    //重置
    const reset = () => {
        count.value = current
    }
    //延迟加
    const lazyadd = () => {
            setTimeout(()=>{
                count.value++
            },delay)
    }
    //延迟减
    const lazyreduce = () => {
            setTimeout(()=>{
                count.value--
            },delay)
    }
    //按数量加
    const rationadd = (num:number) =>{ 
        count.value =  count.value + num!
    }
    //按数量减
    const rationreduce = (num:number) =>{ 
        count.value =  count.value - num!
    }
    return{
        add,reduce,setup,reset,lazyadd,lazyreduce,rationadd,rationreduce,count
    }
}