<template>
  <div>
    <h1>倒计时</h1>
    <span>{{date}}</span>
    <h1>鼠标移动的坐标</h1>
     {{x}}---{{y}}
     <h1>
       计数器
     </h1>
     <div style="display:flex">
        <button class="btn" @click="clkadd">增加</button>
        <button class="btn" @click="clkreduce">减少</button>
        <button class="btn" @click="clksetup">设置</button>
        <button class="btn" @click="clkreset">重置</button>
        <button class="btn" @click="clklazyadd">延迟加</button>
        <button class="btn" @click="clklazyreduce">延迟减</button>
        <button class="btn" @click="clkrationadd">按数量加</button>
        <button class="btn" @click="clkrationreduce">按数量减</button>
        <div>{{count}}</div>
     </div>
     <h1>本地存储</h1>
      <div style="display:flex">
        <div>{{getMessage("obj")}}</div>
         <div @click="setstorage" class="btn">存储localStorage</div>
         <div @click="clearstorage" class="btn">清除localStorage</div>
      </div>
      <div style="display:flex">
         <div>{{getsession("obj")}}</div>
         <div class="btn" @click="setsessionage">存储sessionStorage</div>
         <div class="btn" @click="cleansession">清除sessionStorage</div>
      </div>
  </div>
</template>

<script lang="ts">
import dayjs from "dayjs"
import {useMouse} from "@/hooks/useMouse"
import {useDate} from "@/hooks/useDate"
import {useStorage} from "@/hooks/useStorage"
import {usecounter} from "@/hooks/useCounter"
import { defineComponent, reactive, toRefs, SetupContext, onMounted, watch } from "vue";
interface Obj {
  name:string;
  key:string;
}
interface Data {
  date: string;
  obj: Obj[];
  getstg:any;
  current:number
}
export default defineComponent({
  name: "about",
  props: {},
  components: {},
  setup(props, ctx: SetupContext) {
    const data: Data = reactive<Data>({
       date: "" ,
       obj:[
         {name:'jack',key:"1"},
         {name:'mary',key:"2"}
         ],
       getstg:'',
       current: 0,
    });
    const {x,y} = useMouse()
    const {date} = useDate()
    const {getMessage,setMessage,clearMessage,getsession,setsession,clearsession} = useStorage()
    const {add,reduce,setup,reset,lazyadd,lazyreduce,rationadd,rationreduce,count} = usecounter(data.current,{min:0,max:10},1000)
// localstorage
    const setstorage = () => {
      setMessage("obj",data.obj)
    }
    const clearstorage = () => {
      clearMessage("obj")
    }
// sessionstorage
    const setsessionage = () => {
      setsession("obj",data.obj)
    }
    const cleansession = () => {
      clearsession("obj")
    }
// 计数器
    const clkadd = () => {
      add()
    }
    const clkreduce = () => {
      reduce()
    }
    const clksetup = () => {
      setup(50)
    }
    const clkreset = () => {
      reset()
    }
    const clklazyadd = () => {
      lazyadd()
    }
    const clklazyreduce = () => {
      lazyreduce()
    }
    const clkrationadd = () => {
      rationadd(5)
    }
    const clkrationreduce= () => {
      rationreduce(5)
    }
    watch(()=> data.current,(val)=>{
      data.current=val
    })
    onMounted(()=>{
      console.log(getMessage("obj"))
      console.log(getsession("obj"))
    })
    return {
      ...toRefs(data),x,y,date,getMessage,setMessage,clearMessage,getsession,setsession,clearsession,
      setstorage,clearstorage,setsessionage,cleansession,add,reduce,setup,reset,lazyadd,lazyreduce,rationadd,rationreduce,
      clkadd,clkreduce,clksetup,clkreset,clklazyadd,clklazyreduce,clkrationadd,clkrationreduce,count
    };
  },
});
</script>

<style scoped lang="scss">
h1{
  margin-top:30px;
  margin-bottom: 20px;
}
.btn{
  padding:3px 10px ;
  border: 1px solid black;
  margin: 0 10px;
}
</style>
