export  const  useStorage = () =>{
    const getMessage = (target:any) => {
        if(localStorage.getItem(target) && typeof(JSON.parse(localStorage.getItem(target)!)) === "object"){
            return JSON.parse(localStorage.getItem(target)!)
        } else {
            return localStorage.getItem(target)
        }
    }
    const setMessage = (key:string,target:any) => {
        if(typeof(target) === 'object'){
            return localStorage.setItem(key,JSON.stringify(target))!
        } else {
            return localStorage.setItem(key,target)
        }
    }
    const clearMessage = (key:string) => {
        localStorage.removeItem(key)
    }
    const getsession = (target:any) => {
        if(sessionStorage.getItem(target) && typeof(JSON.parse(sessionStorage.getItem(target)!)) === "object"){
            return JSON.parse(sessionStorage.getItem(target)!)
        } else {
            return sessionStorage.getItem(target)
        }
    }
    const setsession = (key:string,target:any) => {
        if(typeof(target) === 'object'){
            return sessionStorage.setItem(key,JSON.stringify(target))!
        } else {
            return sessionStorage.setItem(key,target)
        }
    }
    const clearsession = (key:string) => {
        sessionStorage.removeItem(key)
    }
    return{
        getMessage,setMessage,clearMessage,getsession,setsession,clearsession
    }
}